.timeline-steps{
    display: flex;
    justify-content: center;
    flex-wrap: wrap
}
.containerss{
    text-align: center;
    color: #fff;
}
.roww{
    padding-bottom: 25px;
}
.roww h2{
    font-weight: 850px;
    font-size: 50px;
    line-height: 45px;
    color: #F49867;
    padding-bottom: 0.6rem;
}
.timeline-content h1{
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 30px;
    text-align: center;
}
.timeline-content p{
    color: var(--color-text);
    font-family: var(--font-family);
    text-align: center;
    font-weight: 550;
    font-size: 20px;
    line-height: 25px;
}
.timeline-steps .timeline-step{
    align-items: center;
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 1rem
}
@media (min-width:768px){
    .timeline-steps .timeline-step:not(:last-child):after{
        content: "";
        display: block;
        border-top: .25rem dotted #F49867;
        width: 6rem;
        position: absolute;
        left: 7.5rem;
        top: .3125rem
    }
    .timeline-steps .timeline-step:not(:first-child):before{
        content: "";
        display: block;
        border-top: .25rem dotted #F49867;
        width: 7.9rem;
        position: absolute;
        right: 7.5rem;
        top: .3125rem
    }
}
@media screen and (max-width: 650px){
    .roww h2{
        font-size: 48px;
        line-height: 60px;
    }
    .timeline-content h1{
        font-size: 25px;
        line-height: 40px;
    }
    .timeline-content p {
        font-size: 16px;
        line-height: 24px;
    }
}
@media screen and (max-width: 490px){
    .roww h2{
        font-size: 36px;
        line-height: 48px;
    }
    .timeline-content h1{
        font-size: 22px;
        line-height: 40px;
    }
    .timeline-content p{
        font-size: 14px;
        line-height: 24px;
    }
}
.timeline-steps .timeline-content{
    width: 13.55rem;
    text-align: center
}
.timeline-steps .timeline-content .inner-circle{
    border-radius: 1.5rem;
    height: 1rem;
    width: 1rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #F49867;
}
.timeline-steps .timeline-content .inner-circle:before{
    content: "";
    background-color: #AE67FA;
    display: inline-block;
    height: 3rem;
    width: 3rem;
    min-width: 3rem;
    border-radius: 6.25rem;
    opacity: .5
}